<h1 id="same-origin-policy-and-cross-origin-resource-sharing" tabindex="-1"><a class="header-anchor" href="#same-origin-policy-and-cross-origin-resource-sharing" aria-hidden="true">#</a> Same-Origin Policy and Cross-Origin Resource Sharing</h1>
<p>AJAX brings great potentials to websites, but having client-side JavaScript code being able to send whichever HTTP requests it wants might not be a good idea. Let's take a look at why, and how web browsers limits which requests client-side JavaScript may send and which responses it may receive.</p>
